import { IonButton, IonButtons, IonCard, IonCardContent, IonCardHeader, IonChip, IonCol, IonContent, IonGrid, IonHeader, IonImg, IonItem, IonItemDivider, IonLabel, IonPage, IonRow, IonText, IonTitle, IonToolbar } from '@ionic/react';
import { useEffect, useState } from 'react';
import LoginButton from '../components/LoginButton';
import './Tab1.css';

export interface NFTBalance {
  token_address: string;
  token_id: string;
  contract_type: string;
  owner_of: string;
  block_number: string;
  block_number_minted: string;
  token_uri?: string | undefined;
  metadata?: string | undefined;
  synced_at?: string | undefined;
  amount?: string | undefined;
  name: string;
  symbol: string;
}
interface NFTMetadata {
  token_address: string;
  name: string;
  abi?: string | undefined;
  supports_token_uri?: number | undefined;
  synced_at?: string | undefined;
  symbol: string;
  contract_type: string
}

export const NFTCard: React.FC<{ nft: NFTBalance }> = ({ nft }) => {
  const [nftMeta, setNFTMetadata] = useState<any | undefined>(nft.metadata)
  useEffect(() => {
    (async () => {

      // const meta = await fetch(nft.token_uri.replace("ipfs://", "https://smol.mypinata.cloud/ipfs/"))
      // setNFTMetadata(await meta.json());
    })()

  }, [nft])
  return <IonCard title={nft.name} color="light">
    <IonCardHeader color="paper">
      <IonTitle size='large'>
        <h2>
          {nft.name || nft.symbol || (nftMeta && nftMeta.description)}
        </h2>
      </IonTitle>

    </IonCardHeader>
    <IonCardContent>
      {nftMeta && nftMeta.image && <IonImg style={{ width: 612, height: 612 }} src={nftMeta.image.replace("ipfs://", "https://smol.mypinata.cloud/ipfs/")} />}
      <IonText color="medium">
        <IonItemDivider >
          {nftMeta && nftMeta.description || ""}
        </IonItemDivider>
      </IonText>
      <IonItemDivider color="clear" />
      {Object.values(nftMeta.attributes).map((x: any, i) => <IonChip key={i} color="medium">
        {x && x.trait_type && < IonLabel color="tertiary">
          {String(x.trait_type)}:
        </IonLabel>}
        {x && x.value && String(x.value)}
      </IonChip>)}
    </IonCardContent>
  </IonCard >
}


export const MagePunkCard: React.FC<{ metadata: any }> = ({ metadata }) => {
  const nft = metadata;
  const nftMeta = metadata;
  return <IonCard title={nft.name}>
    <IonItem color="tertiary">
      <IonTitle>
        {nft.name || nft.symbol || (nftMeta && nftMeta.description)}
      </IonTitle>
    </IonItem>
    <IonCardContent>
      {nftMeta && nftMeta.image && <IonImg src={nftMeta.image.replace("ipfs://", "https://smol.mypinata.cloud/ipfs/")} />}

      <IonText color="medium">
        {nftMeta && nftMeta.description || ""}
      </IonText>
      <IonItemDivider color="clear" />
      {Object.values(nftMeta.attributes).map((x: any) => <IonChip color="medium">
        {x && x.trait_type && < IonLabel color="tertiary">
          {String(x.trait_type)}:
        </IonLabel>}
        {x && x.value && String(x.value)}
      </IonChip>)}
    </IonCardContent>
  </IonCard >
}

const Tab1: React.FC = () => {
  const [nfts, setNFTs] = useState<NFTBalance[]>()
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Magepunks</IonTitle>
          <IonButtons slot="end">
            <LoginButton />
          </IonButtons>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonGrid>

          <IonRow>
            {nfts && nfts.map(nft => <IonCol><NFTCard nft={nft} /></IonCol>)}
          </IonRow>
        </IonGrid>
      </IonContent>
    </IonPage >
  );
};

export default Tab1;
